      {
        title: "혼주",
        name: "김철희",
        bankName: "신협",
        accountNum: "132-025-192495",
        kakaoPay: "",
      },
      {
        title: "혼주",
        name: "윤선희",
        bankName: "대구은행",
        accountNum: "010-9493-1884",
        kakaoPay: "",
      },
      {
        title: "신랑",
        name: "김준우",
        bankName: "신한은행",
        accountNum: "110-492-402330",
        kakaoPay: "https://qr.kakaopay.com/Ej8yPz1gy",
      },
    ];

    const ssoAccountInfo = [
      {
        title: "혼주",
        name: "진도식",
        bankName: "농협",
        accountNum: "298-12-218522",
        kakaoPay: "",
      },
      {
        title: "신부",
        name: "진소정",
        bankName: "국민은행",
        accountNum: "801302-01-590439",
        kakaoPay: "https://qr.kakaopay.com/Ej9QgVq1A",
      },
    ];

    const dday = ref("");
    var ddayInterval = null;

    onMounted(() => {
      window.kakao && window.kakao.maps ? initMap() : addKakaoMapScript();

      const btn = document.querySelector(".sms-share-btn");
      btn.addEventListener("click", () => {
        var mobileType = checkMobile();
        checkM(mobileType);
      });

      // screen size
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);

      // setScreenSize();
      // window.addEventListener("resize", () => {
      //   setScreenSize();
      // });

      // scroll animation
      window.addEventListener("scroll", function () {
        scrollAnimation(".reveal");
        scrollAnimation(".text-focus-in");
      });

      function scrollAnimation(className) {
        var sections = document.querySelectorAll(className);
        for (var i = 0; i < sections.length; i++) {
          var windowHeight = window.innerHeight;
          var elementTop = sections[i].getBoundingClientRect().top;
          var elementVisible = 50;

          if (elementTop < windowHeight - elementVisible) {
            sections[i].classList.add("active");
          } else {
            sections[i].classList.remove("active");
          }
        }
      }

      ddayInterval = setInterval(() => {
        dday.value = getDday();
      }, 1000);
    });

    // kakao
    function addKakaoMapScript() {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () =>
        kakao.maps.load(() => {
          initMap();
        });

      script.src = `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY}`;
      document.head.appendChild(script);
    }

    function initMap() {
      var container = document.getElementById("map");
      var options = {
        center: new kakao.maps.LatLng(35.883487491158675, 128.65892377049653),
        level: 5,
      };

      var map = new kakao.maps.Map(container, options);

      for (var index = 0; index < markerInfos.length; index++) {
        var imageSrc = require(`@/assets/${markerInfos[index].img}`);
        // 마커 이미지의 이미지 크기 입니다
        var imageSize = new kakao.maps.Size(
          markerInfos[index].size,
          markerInfos[index].size
        );

        // 마커 이미지를 생성합니다
        var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

        // 마커를 생성합니다
        var marker = new kakao.maps.Marker({
          map: map, // 마커를 표시할 지도
          position: new kakao.maps.LatLng(
            markerInfos[index].lat,
            markerInfos[index].lng
          ), // 마커를 표시할 위치
          title: markerInfos[index].title, // 마커의 타이틀, 마커에 마우스를 올리면 타이틀이 표시됩니다
          image: markerImage, // 마커 이미지
        });

        marker.setMap(map);
      }
    }

    // 카카오 공유
    function shareKakao() {
      window.Kakao.Share.sendDefault({
        objectType: "location",
        address: "대구 동구 동촌로 200",
        addressTitle: "퀸벨호텔 1층 테라하우스홀",
        content: {
          title: "김준우🤍진소정 결혼식에 초대합니다.",
          description:
            "2022년 09월 24일 토요일 오후 1시 퀸벨호텔 1층 테라하우스홀",
          imageUrl: `${url}/kakaoShare.jpg`,
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
        // social: {
        //   likeCount: 286,
        //   commentCount: 45,
        //   sharedCount: 845,
        // },
        buttons: [
          {
            title: "청첩장 보기",
            link: {
              mobileWebUrl: url,
              webUrl: url,
            },
          },
        ],
      });
    }

    // 문자 공유하기

    function checkM(m) {
      location.href =
        "sms:" +
        "" +
        (m == "ios" ? "&" : "?") +
        "body=" +
        encodeURIComponent("김준우🤍진소정 결혼식에 초대합니다.\n" + url);
    }

    function checkMobile() {
      var varUA = navigator.userAgent.toLowerCase(); //userAgent 값 얻기
      if (varUA.indexOf("android") > -1) {
        //안드로이드
        return "android";
      } else if (
        varUA.indexOf("iphone") > -1 ||
        varUA.indexOf("ipad") > -1 ||
        varUA.indexOf("ipod") > -1
      ) {
        //IOS
        return "ios";
      } else {
        //아이폰, 안드로이드 외
        return "other";
      }
    }

    // check mobile and kakao pay
    function sendKakaoPay(url) {
      var isMobile =
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        );

      if (isMobile) {
        location.href = url;
      } else {
        window.alert("카카오페이는 모바일에서 사용할 수 있습니다.");
      }
    }

    // copy
    function copy(text) {
      copyText(text, undefined, (error, event) => {
        if (error) {
          alert("Can not copy");
          console.log(error);
        } else {
          alert("복사되었습니다.");
          console.log(event);
        }
      });
    }

    // d-day 계산
    function getDday() {
      // var masTime = new Date("2022-09-24");
      const masTime = new Date("2022-09-24T13:00:00+0900");
      const todayTime = new Date();

      const diff = masTime - todayTime;

      const diffDay = Math.floor(diff / (1000 * 60 * 60 * 24));
      const diffHours = Math.floor((diff / (1000 * 60 * 60)) % 24);
      const diffMinutes = Math.floor((diff / (1000 * 60)) % 60);
      const diffSeconds = Math.floor((diff % (1000 * 60)) / 1000);

      if (diff > 0) {
        var day = diffDay > 0 ? `${diffDay}일` : "";
        var hours = diffHours > 0 ? `${diffHours}시간` : "";
        var minutes = diffMinutes > 0 ? `${diffMinutes}분` : "";
        var seconds = diffSeconds > 0 ? `${diffSeconds}초` : "";

        console.log("interval");

        return `${day != "" ? `${day} ` : ""}
      ${hours != "" ? `${hours} ` : ""}
      ${minutes != "" ? `${minutes} ` : ""}
      ${seconds != "" ? `${seconds}` : ""}`;
      } else {
        if (ddayInterval) {
          clearInterval(ddayInterval);
        }
        return `측하해 주신 모든 분들에게 감사드립니다.`;
      }
    }

    // const attrs = {
    //   key: "today",
    //   dates: new Date(),
    //   highlight: true,
    // };
    return {
      parentsComp,
      accountTextComp,
      imageIndex,
      images,
      junAccountInfo,
      ssoAccountInfo,
      shareKakao,
      sendKakaoPay,
      copy,
      getDday,
      dday,
    };
  },
};
</script>

